<template>
  <cell :title="title" @click.native="show = true" is-link>
    <modal direction="bottom" v-model="show" class="picker">
      <flex justify="space-between" style="padding:0 1rem;" class="picker-header">
        <ly-button type="text" @click="cancel"></ly-button>
        <ly-button type="text" @click="confirm"></ly-button>
      </flex>
      <div v-if="single" id="scroller"></div>
      <flex v-if="!single">
        <flex-item :span="1" v-for="(item, index) in data" :key="index">
          <div :id="`scroller_${index}`"></div>
        </flex-item>
      </flex>
      <flex v-if="columns">
        <flex-item :span="1" v-for="(item, index) in [0, 1, 2]" :key="index">
          <div :id="`scroller_v_${index}`"></div>
        </flex-item>
      </flex>
    </modal>
    <span>{{currentValue.join('/')}}</span>
  </cell>
</template>

<script>
import Scroller from './scroller.js'
export default {
  name: 'picker',
  data () {
    return {
      show: false, // 
      currentValue: this.value, // 
      selectValue: this.value, // 
      scroller: [],
      colList: []
    }
  },
  computed: {
    single () { // 
      return this.value.length < this.data.length
    }
  },
  props: {
    title: String,
    value: [Boolean, String, Array, Number], // 
    data: Array, // 
    columns: Number
  },
  mounted () {
    const _this = this
    // data[[],[]][{}]
    if (!this.columns) {
      if (_this.single) {
        _this.data[0] = _this.data
        _this.renderList(0, '#scroller')
      } else {
        _this.data.map((el, i) => {
          _this.renderList(i, `#scroller_${i}`)
        })
      }
    } else {
      // value
      // 
      this.renderChain(0)
    }
  },
  methods: {
    cancel (e) {
      e.stopPropagation()
      this.show = false
    },
    confirm (e) { // value
      e.stopPropagation()
      this.show = false
      this.currentValue = this.selectValue
    },
    renderList (i, id) {
      const _this = this
      this.scroller[i] = new Scroller(id, {
        data: _this.data[i],
        defaultValue: _this.value[i],
        onSelect (val) {
          _this.selectValue[i] = val
        }
      })
    },
    renderChain (i) {
      const _this = this
      let scroller = []
      scroller[i] = new Scroller(`#scroller_v_${i}`, {
        data: i === 0 ? _this.find(0) : _this.find(_this.selectValue[i - 1]),
        defaultValue: 0,
        onSelect (val) {
          _this.selectValue[i] = val
          _this.renderChain(i + 1)
          console.log('', _this.selectValue[i], _this.selectValue)
        }
      })
    },
    find (t) {
      let list = []
      this.data.forEach((el, i) => {
        if (el.parent === t) {
          list.push(el)
        }
      })
      return list
    }
  },
  watch: {
    value (val) {
      this.currentValue = val
    },
    currentValue (val) {
      this.$emit('input', val)
    }
  }
}
</script>

<style lang="sass">
@import  "./scroller.scss";
@import  "../theme/picker.scss";
@import  "../theme/common/animate.scss";
</style>



